import { Component, OnInit, Output, EventEmitter, ViewEncapsulation, Input, forwardRef } from '@angular/core';
import { ISplitInput, ISplitInputFecha, ISplitInputDomicilio, ISplitInputDocumento, ISplitInputDomicilioPA } from './split-input.model';
import { FormGroup, FormBuilder, Validators, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'app-split-input',
  templateUrl: './split-input.component.html',
  styleUrls: ['./split-input.component.scss'],
  encapsulation: ViewEncapsulation.None
  // providers: [
  //   {
  //     provide: NG_VALUE_ACCESSOR,
  //     useExisting: forwardRef(() => SplitInputComponent),
  //     multi: true
  //   }
  // ]
})
export class SplitInputComponent implements OnInit {

  @Input() type: 'documento' | 'domicilio' | 'domicilioPa' | 'fecha';
  @Input() fecha: ISplitInputFecha;
  @Input() domicilio: ISplitInputDomicilio;
  @Input() domicilioPA: ISplitInputDomicilioPA;
  @Input() documento: ISplitInputDocumento;
  @Output() idOnChange = new EventEmitter();
  formDocumento: FormGroup;
  formDomicilio: FormGroup;
  formDomicilioPA: FormGroup;
  formFecha: FormGroup;

  constructor(public fb: FormBuilder) {}
  ngOnInit() {
    this.createForm();
  }

  onChange(i, event) {
    const valChanged = this.getValueByType;
    this.idOnChange.emit(valChanged);
    return valChanged;
  }

  get isTypeDocumento(): boolean {
    return this.type === 'documento';
  }

  get isTypeDomicilio(): boolean {
    return this.type === 'domicilio';
  }

  get isTypeFecha(): boolean {
    return this.type === 'fecha';
  }

  get isTypeDomicilioPA(): boolean {
    return this.type === 'domicilioPa';
  }

  get getValueByType(): any {
    if (this.isTypeDocumento) {
      return this.getValDocumento;
    } else if (this.isTypeDomicilio) {
      return this.getValDomicilio;
    } else if (this.isTypeFecha) {
      return this.getValFecha;
    } else if (this.isTypeDomicilioPA) {
      return this.getValDomicilioPA;
    }
  }

  get getValDocumento(): any {
    return {
      isValid: this.formDocumento.valid,
      value: this.formDocumento.value
    };
  }

  get getValDomicilio(): any {
    return {
      isValid: this.formDomicilio.valid,
      value: this.formDomicilio.value
    };
  }

  get getValDomicilioPA(): any {
    return {
      isValid: this.formDomicilioPA.valid,
      value: this.formDomicilioPA.value
    };
  }

  get getValFecha(): any {
    return {
      isValid: this.formFecha.valid,
      value: this.formFecha.value
    };
  }

  createForm() {
    if (this.isTypeFecha) {
      if (!this.fecha) {
        this.fecha = { dia: '', mes: '', anio: '' };
      }
      this.formFecha = this.fb.group({
        dia: [this.fecha.dia, [Validators.required]],
        mes: [this.fecha.mes, [Validators.required]],
        anio: [this.fecha.anio, [Validators.required]],
      });
    } else if (this.isTypeDomicilio) {
      if (!this.domicilio) {
        this.domicilio = { corregimientos: [], provincias: [], distritos: [] };
      }
      this.formDomicilio = this.fb.group({
        provincia: [0, [Validators.required]],
        distrito: [0, [Validators.required]],
        corregimiento: [0, [Validators.required]]
      });
    } else if (this.isTypeDocumento) {
      if (!this.documento) {
        this.documento = { parte1: '', parte2: '', parte3: '' };
      }
      this.formDocumento = this.fb.group({
        part1: [this.documento.parte1, [Validators.required]],
        part2: [this.documento.parte2, [Validators.required]],
        part3: [this.documento.parte3, [Validators.required]]
      });
    } else if (this.isTypeDomicilioPA) {
      if (!this.domicilioPA) {
        this.domicilioPA = { barrios: [], colonias: [], municipios: [] };
      }
      this.formDomicilioPA = this.fb.group({
        colonia: [0, [Validators.required]],
        barrio: [0, [Validators.required]],
        municipio: [0, [Validators.required]],
      });
    }
  }
}
